/* General body styles */
body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #f9f9f9;
}

/* App container */
.App {
  text-align: center;
}

/* Header styles */
.app-header {
  background-color: #2ecc71;  /* Green */
  padding: 1rem 2rem;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Navigation and Authentication buttons section */
.nav-and-auth {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/* Navigation links */
.nav-links {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 1.5rem;
  margin: 0;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #dfffe6;  /* Light Green */
}

/* Authentication buttons section */
.auth-buttons {
  display: flex;
  gap: 1rem;
}

.auth-buttons button {
  background-color: white;
  color: #2ecc71;  /* Green */
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
}

.auth-buttons button:hover {
  background-color: #27ae60;  /* Darker Green */
  color: white;
}

/* Page main content styles */
main {
  padding: 3rem;
  margin-top: 50px;
}
